*, *:before, *:after {
  border: 0 none;
  box-sizing: border-box;
  font-family: inherit;
  outline: none !important;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  height: 100%;
  width: 100%;
}

body {
  min-height: 100%;
  position: relative;
}

img {
  vertical-align: middle;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

strong, b {
  font-weight: 700;
}

em, i {
  font-style: italic;
}